}

.services .box:hover .icon {
    color: #000000;
}

.services .box:hover .icon {
    color: #000000;
}

.services .box:hover  p {
    color: #ffffff ;
}


:root {
    --primary-gray: #ccd1d9; 
    --primary-dark-gray: #666666;
}

.experience-wrapper {
    position: relative;
    padding: 3.125rem;
    overflow: hidden;
}

.experience h1 {
    text-transform: uppercase;
    color : rgb(0, 191, 255);
}

.experience-wrapper:before {
    position: absolute;
    content: "";
    top: 0;
    left: 50%;
    margin-left: -0.0626rem;
    width: 0.125rem;
    height: 100%;
    background: #ccd1d9;
    background: var(--primary-gray);
    z-index: 1;
    margin-bottom: 2rem;
}

.timeline-block {
    width: calc(50% + 0.5rem);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    clear: both;
}

.timeline-block-right {
    float: right;
}   

.timeline-block-left {
    float: left;
    direction: rtl;
}

.marker { 
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 00.3125rem solid #666666;
    border: 00.3125rem solid var(--primary-dark-gray);
    background: rgb(0, 191, 255);
    margin-top: 0.625rem;
    z-index: 3;
}

.timeline-content {
    width: 95%;
    padding: 0 0.09375rem;
    color: #666666;
    color: var(--primary-dark-gray);
    direction: ltr;
}

.timeline-content h3 {
    margin-top: 0.3125rem;
    font-weight: 900;
}

/*----------------------------MEDIA QUERIES---------------------------------*/

@media(max-width: 768px){

    .experience {
        padding: 0.7rem;
    }

   .experience .container {
       padding-left: 0;
   }
   
    .experience-wrapper:before {
        left: 0.5rem;
        margin-bottom: 1.875rem;
    }

    .timeline-block-right {
        float: none;
        width: 100%;
    }

    .timeline-block-left {
        float: none;
        direction: ltr;
        width: 100%;
    }
}
.contacts {
    background: rgb(52, 58, 64);
    padding: 3rem 0;
}

.contacts h1{
    text-transform: uppercase;
    color: rgb(0, 191, 255);
    padding: 1.3rem 0;;
}

.contacts p {
    color: #D3D3D3;
    font-size: 1.2rem
}

.contacts input, .contacts textarea {
    width: 100%;
    box-sizing: border-box;
    background: none;
    resize: none;
    border: 0;
    border-bottom: 0.125rem solid lightgray;
    border-radius: 0;
    color: #808080;
    outline: 0 !important;
    box-shadow: none !important;
    top: 0;
    margin-bottom: 1rem;
    padding-left: 0 !important;
}
/* 
.contacts input:focus, .contacts textarea:focus {

} */

input.form-control, textarea.form-control {
    margin: -0.3rem 0;
}
.contacts container {
