}

.line {
    position: relative;
    top: -0.8rem;
    width: 0;
    height: 0.15rem;
    background: rgb(0, 191, 255);
    display: inline-block;
    top: -0.8rem;
}

input:focus+.line, textarea:focus+.line {
    width: 100%;
    transition: width 0.5s ease-in-out

}

.error-message{
